/* Contents */
.toolbar-search {
  position: relative;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 4px 8px; }
  .toolbar_size_small .toolbar-search {
    width: 0; }
  .toolbar_size_medium .toolbar-search {
    width: 300px; }
  .toolbar_size_normal .toolbar-search {
    width: 360px; }
  .toolbar_size_big .toolbar-search {
    width: 0; }
  .toolbar-search__input {
    box-sizing: border-box;
    display: block;
    align-self: stretch;
    flex-grow: 1;
    margin: 6px 4px;
    width: auto;
    border: none;
    z-index: 3;
    outline: none;
    color: #313131;
    background-color: transparent;
    transition: color 0.3s ease-out 0s; }
    .toolbar-search__input:-webkit-autofill {
      border: none;
      -webkit-text-fill-color: #313131;
      -webkit-box-shadow: 0 0 0 100px #fff inset; }
    .toolbar-search__input::-webkit-search-cancel-button {
      -webkit-appearance: none; }
    .toolbar-search__input::-webkit-input-placeholder {
      color: #68686a;
      transition: color 0.3s ease-out 0s; }
    .toolbar-search__input::-moz-placeholder {
      color: #68686a;
      transition: color 0.3s ease-out 0s; }
    .toolbar-search__input:-ms-input-placeholder {
      color: #68686a;
      transition: color 0.3s ease-out 0s; }
    .toolbar-search__input::-ms-input-placeholder {
      color: #68686a;
      transition: color 0.3s ease-out 0s; }
    .toolbar-search__input::placeholder {
      color: #68686a;
      transition: color 0.3s ease-out 0s; }
    .toolbar-search:hover .toolbar-search__input[value=""]:not(:focus)::-webkit-input-placeholder {
      color: #fff; }
    .toolbar-search:hover .toolbar-search__input[value=""]:not(:focus)::-moz-placeholder {
      color: #fff; }
    .toolbar-search:hover .toolbar-search__input[value=""]:not(:focus):-ms-input-placeholder {
      color: #fff; }
    .toolbar-search:hover .toolbar-search__input[value=""]:not(:focus)::-ms-input-placeholder {
      color: #fff; }
    .toolbar-search:hover .toolbar-search__input[value=""]:not(:focus)::placeholder {
      color: #fff; }
    .toolbar-search__input:focus::-webkit-input-placeholder {
      color: #bdbdbd; }
    .toolbar-search__input:focus::-moz-placeholder {
      color: #bdbdbd; }
    .toolbar-search__input:focus:-ms-input-placeholder {
      color: #bdbdbd; }
    .toolbar-search__input:focus::-ms-input-placeholder {
      color: #bdbdbd; }
    .toolbar-search__input:focus::placeholder {
      color: #bdbdbd; }
    .toolbar-search:hover .toolbar-search__input[value=""]:not(:focus) {
      color: #fff; }
  .toolbar-search__submit, .toolbar-search__reset {
    box-sizing: border-box;
    display: block;
    border-radius: 50%;
    border: none;
    z-index: 3;
    pointer-events: none;
    cursor: auto;
    outline: none; }
  .toolbar-search__submit {
    order: -1;
    margin: 2px 4px;
    padding: 7px;
    width: 28px;
    height: 28px;
    color: #68686a;
    background-color: #29292c;
    transition: background-color 0.3s ease-out 0s, color 0.3s ease-out 0s; }
    .toolbar-search:hover .toolbar-search__input[value=""]:not(:focus) ~ .toolbar-search__submit {
      color: #fff; }
    .toolbar-search__input:focus ~ .toolbar-search__submit,
    .toolbar-search__input:not([value=""]) ~ .toolbar-search__submit {
      pointer-events: auto;
      cursor: pointer;
      color: #313131;
      background-color: #f2f2f5; }
    .toolbar-search__input:focus ~ .toolbar-search__submit:hover,
    .toolbar-search__input:not([value=""]) ~ .toolbar-search__submit:hover {
      color: #313131;
      background-color: #ebebeb; }
  .toolbar-search__reset {
    margin: 2px;
    padding: 2px;
    width: 24px;
    height: 24px;
    color: #bdbdbd;
    background-color: transparent;
    opacity: 0;
    transition: opacity 0.3s ease-out 0s, color 0.3s ease-out 0s; }
    .toolbar-search__input:focus ~ .toolbar-search__reset,
    .toolbar-search__input:not([value=""]) ~ .toolbar-search__reset {
      pointer-events: auto;
      cursor: pointer;
      opacity: 1; }
    .toolbar-search__input:focus ~ .toolbar-search__reset:hover,
    .toolbar-search__input:not([value=""]) ~ .toolbar-search__reset:hover {
      pointer-events: auto;
      cursor: pointer;
      color: #313131;
      opacity: 1; }
  .toolbar-search__background {
    position: absolute;
    display: block;
    margin: 0 -8px;
    width: 100%;
    height: 100%;
    border-radius: 4px;
    z-index: 2;
    pointer-events: none;
    transition: border-radius 0.3s ease-out 0s, background-color 0.3s ease-out 0s; }
    .toolbar_size_small .toolbar-search__background {
      background: transparent; }
    .toolbar_size_medium .toolbar-search__background {
      background: #333336; }
    .toolbar_size_normal .toolbar-search__background {
      background: transparent; }
    .toolbar_size_big .toolbar-search__background {
      background: transparent; }
    .toolbar-search__input:focus ~ .toolbar-search__background,
    .toolbar-search__input:not([value=""]) ~ .toolbar-search__background {
      background-color: #fff; }
  .toolbar-search__input:not([value=""]):focus ~ .toolbar-search__suggestions {
    pointer-events: auto;
    opacity: 1; }
  .toolbar-search__input:not([value=""]):focus ~ .toolbar-search__suggestions ~ .toolbar-search__background {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0; }
