/* Misc */
/* Contents */
.message {
  display: block;
  flex-shrink: 0;
  margin: auto; }
  .message__icon {
    display: block;
    box-sizing: border-box;
    border-radius: 50%;
    margin: 16px auto;
    padding: 24px;
    width: 104px;
    height: 104px;
    color: #fff; }
    .message_status_success .message__icon {
      background-color: #21b15e; }
    .message_status_warning .message__icon {
      background-color: #e8bb27; }
    .message_status_danger .message__icon {
      background-color: #c83f3f; }
    .message_status_info .message__icon {
      background-color: #584ac6; }
  .message__title {
    display: block;
    margin: 8px auto;
    max-width: 480px;
    text-align: center; }
  .message__text {
    display: block;
    margin: 8px auto;
    max-width: 400px;
    text-align: center; }
    .message__text a {
      transition: color 0.3s ease-out 0s; }
      .message__text a:not(:hover):not(:active):not(:focus), .message__text a:link:not(:hover):not(:active):not(:focus), .message__text a:visited:not(:hover):not(:active):not(:focus) {
        color: #584ac6;
        text-decoration: none; }
      .message__text a:hover, .message__text a:active, .message__text a:focus {
        color: #5448ba;
        text-decoration: underline; }
