/* Contents */
.schedule-timeline {
  display: block;
  background-color: #212124; }
  .schedule-timeline__hour {
    position: relative;
    display: flex;
    flex-direction: column; }
    .schedule-timeline__hour__title {
      position: absolute;
      top: -13px;
      left: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 26px;
      color: #fff;
      text-align: center;
      transition: font-size 0.3s ease-out 0s, line-height 0.3s ease-out 0s; }
  .schedule-timeline__quarter {
    display: block;
    margin: 40px auto -1px auto;
    border-radius: .5px;
    width: 16px;
    height: 1px;
    background-color: #68686a; }
    .schedule-timeline__quarter:last-child {
      margin-bottom: 39px; }
