/* Sizing */
/* Typography */
.toolbar-interval-picker__control {
  font-size: 0;
  line-height: 0; }

.toolbar-interval-picker {
  font-size: 15px;
  line-height: 20px; }

/* Colors */
/* Misc */
.toolbar-interval-picker_shown .toolbar-interval-picker__list {
  box-shadow: 0 8px 16px rgba(49, 49, 49, 0.08); }

/* Contents */
.toolbar-interval-picker {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 260px;
  border-radius: 4px;
  background-color: #333336;
  transition: background-color 0.3s ease-out 0s; }
  .toolbar-interval-picker:hover {
    background-color: #3b3b3d; }
  .toolbar-interval-picker__value {
    position: relative;
    display: block;
    flex-grow: 1;
    align-self: stretch;
    margin: 0 6px 0 0; }
  .toolbar-interval-picker__list {
    position: absolute;
    display: block;
    flex-grow: 1;
    align-self: stretch;
    margin: 0 2px;
    width: 100%;
    max-height: 120px;
    border-radius: 2px;
    overflow: auto;
    z-index: 1; }
  .toolbar-interval-picker__option {
    display: none;
    padding: 11px 8px 9px 12px;
    color: #fff;
    text-decoration: none; }
    .toolbar-interval-picker__option_current {
      display: block; }
    .toolbar-interval-picker:not(.toolbar-interval-picker_shown):hover .toolbar-interval-picker__option {
      color: #dedede; }
    .toolbar-interval-picker_shown .toolbar-interval-picker__option {
      display: block;
      color: #313131;
      background-color: #fff; }
      .toolbar-interval-picker_shown .toolbar-interval-picker__option_current, .toolbar-interval-picker_shown .toolbar-interval-picker__option:hover {
        background-color: #f8f8f8; }
  .toolbar-interval-picker__control {
    display: block;
    box-sizing: border-box;
    flex-shrink: 0;
    border-radius: 50%;
    color: #fff;
    background-color: #212124;
    transition: background-color 0.3s ease-out 0s;
    text-decoration: none;
    outline: none; }
    .toolbar-interval-picker__control_drop {
      margin: 8px 12px 8px 6px;
      padding: 8px;
      width: 24px;
      height: 24px; }
    .toolbar-interval-picker__control_previous, .toolbar-interval-picker__control_next {
      margin: 4px 2px;
      padding: 10px;
      width: 32px;
      height: 32px; }
    .toolbar-interval-picker:hover .toolbar-interval-picker__control:not(:hover) {
      background-color: #313131; }
    .toolbar-interval-picker__control:hover {
      background-color: #29292c; }
