/* Sizing */
/* Typography */
.toolbar-suggestions__option__image {
  font-size: 0;
  line-height: 0; }

/* Colors */
/* Misc */
.toolbar-suggestions {
  box-shadow: 0 8px 16px rgba(49, 49, 49, 0.08); }

/* Contents */
.toolbar-suggestions {
  position: absolute;
  display: block;
  width: 100%;
  max-height: 120px;
  top: 100%;
  left: 0;
  border-top: 1px solid #e9e9e9;
  border-radius: 0 0 4px 4px;
  opacity: 0;
  pointer-events: none;
  z-index: 1;
  overflow: auto;
  background-color: #fff;
  transition: opacity 0.3s ease-out 0s; }
  .toolbar-suggestions:hover {
    pointer-events: auto; }
  .toolbar-suggestions__option {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 8px;
    text-decoration: none;
    outline: none;
    cursor: pointer;
    background-color: transparent;
    transition: background-color 0.3s ease-out 0s; }
    .toolbar-suggestions__option:hover {
      background-color: #f8f8f8; }
    .toolbar-suggestions__option > * {
      margin: 0 4px; }
    .toolbar-suggestions__option__image {
      box-sizing: border-box;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      transition: background-color 0.3s ease-out 0s, color 0.3s ease-out 0s;
      background-color: #f2f2f5;
      background-repeat: no-repeat;
      background-size: cover;
      margin-left: 8px;
      width: 24px;
      height: 24px;
      overflow: hidden; }
    .toolbar-suggestions__option__text {
      display: block;
      flex-grow: 1;
      margin-right: 8px; }
  .toolbar-suggestions__empty {
    display: block;
    padding: 11px 16px 9px 16px;
    color: #bdbdbd;
    background-color: #fff; }
