/* Sizing */
/* Typography */
.schedule-channel__image {
  font-size: 0;
  line-height: 0; }

.schedule-channel__rating {
  font-size: 12px;
  line-height: 18px; }

.schedule-channel {
  font-size: 17px;
  line-height: 22px; }

/* Colors */
/* Misc */
.schedule-channel {
  box-shadow: 0 1px 0 rgba(49, 49, 49, 0.11); }

/* Contents */
.schedule-channel {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 4px;
  text-decoration: none;
  outline: none;
  height: 100%;
  padding: 8px 4px;
  color: #313131;
  background-color: #fff;
  transition: background-color 0.3s ease-out 0s; }
  .schedule-channel:hover, .schedule-channel:focus {
    background-color: #fbfbfb; }
  .schedule-channel__image {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    overflow: hidden;
    margin: 5px 8px;
    width: 32px;
    height: 32px;
    background-repeat: no-repeat;
    background-size: cover; }
  .schedule-channel__caption {
    display: flex;
    flex-direction: column;
    align-items: stretch; }
  .schedule-channel__name, .schedule-channel__rating {
    display: block; }
