/* Contents */
html {
  font-family: "Roboto", Arial, Helvetica, sans-serif; }

body {
  color: #313131;
  background-color: #f8f8f8; }

.state {
  display: none; }

.iconset {
  display: none; }

.container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background-color: #f2f2f5; }

.header__toolbar__logo .toolbar-button__image {
  border-radius: unset; }

.main {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow: auto;
  align-items: center; }

.modals {
  box-sizing: border-box;
  position: absolute;
  display: flex;
  padding: 16px;
  width: 100%;
  height: 100%;
  z-index: 10;
  overflow: auto; }
  .modals > *:not(.modals__background) {
    z-index: 2; }
  .modals__background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(49, 49, 49, 0.8);
    z-index: 1; }
