/* Sizing */
/* Typography */
.toolbar_size_small {
  font-size: 12px;
  line-height: 18px; }

.toolbar_size_medium {
  font-size: 15px;
  line-height: 20px; }

.toolbar_size_normal {
  font-size: 17px;
  line-height: 22px; }

.toolbar_size_big {
  font-size: 20px;
  line-height: 26px; }

/* Colors */
/* Misc */
/* Contents */
.toolbar {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  align-self: stretch;
  flex-shrink: 0; }
  .toolbar_size_small {
    padding: 0;
    background-color: transparent; }
    .toolbar_size_small > * {
      margin: 0; }
  .toolbar_size_medium {
    padding: 12px;
    background-color: #2a292c; }
    .toolbar_size_medium > * {
      margin: 0 12px; }
  .toolbar_size_normal {
    padding: 8px 24px;
    background-color: #212124; }
    .toolbar_size_normal > * {
      margin: 0 12px; }
  .toolbar_size_big {
    padding: 0;
    background-color: transparent; }
    .toolbar_size_big > * {
      margin: 0; }
  .toolbar > *:first-child {
    margin-left: 0; }
  .toolbar > *:last-child {
    margin-right: 0; }
