/* Contents */
.toolbar-checkbox {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-decoration: none;
  outline: none; }
  .toolbar-checkbox:not(:first-child) {
    margin-left: 4px; }
  .toolbar-checkbox__check {
    display: block;
    border: 1px solid #595961;
    border-radius: 4px;
    padding: 1px;
    width: 16px;
    height: 16px;
    color: #fff;
    transition: border-color 0.3s ease-out 0s, color 0.3s ease-out 0s; }
    .toolbar-checkbox:hover > .toolbar-checkbox__check,
    .toolbar-checkbox:active > .toolbar-checkbox__check,
    .toolbar-checkbox:focus > .toolbar-checkbox__check {
      border-color: #fff;
      color: #584ac6; }
    .toolbar-checkbox__check svg {
      opacity: 0;
      transition: opacity 0.3s ease-out 0s; }
      .toolbar-checkbox_checked .toolbar-checkbox__check svg {
        opacity: 1; }
  .toolbar-checkbox__label {
    margin-left: 8px;
    color: #fff;
    transition: color 0.3s ease-out 0s; }
    .toolbar-checkbox:hover > .toolbar-checkbox__label,
    .toolbar-checkbox:active > .toolbar-checkbox__label,
    .toolbar-checkbox:focus > .toolbar-checkbox__label {
      color: #584ac6; }
