/* Contents */
.table {
  display: block;
  margin: 24px;
  width: 688px;
  border-radius: 4px;
  background-color: #fff; }
  .table__header {
    display: flex;
    flex-direction: row;
    align-items: stretch; }
    .table__header__title {
      box-sizing: border-box;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-grow: 1;
      padding: 17px 8px 15px 24px;
      font-weight: bold; }
      .table__header__title__more {
        box-sizing: border-box;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-shrink: 0;
        margin-left: 8px;
        padding: 8px;
        border-radius: 50%;
        background-color: #f2f2f5;
        transition: background-color 0.3s ease-out 0s; }
        .table__header__title__more:hover {
          background-color: #e9e9e9; }
        .table__header__title__more > svg {
          width: 8px;
          height: 8px; }
    .table__header__rating {
      box-sizing: border-box;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      padding: 17px 8px 15px 8px;
      width: 64px;
      border-left: 2px solid #f6f6f6; }
      .table__header__rating > svg {
        width: 16px;
        height: 16px; }
  .table__body {
    display: block; }
  .table__row {
    display: flex;
    flex-direction: row;
    align-items: stretch;
    text-decoration: none;
    transition: background-color 0.3s ease-out 0s; }
    .table__row:nth-child(odd) {
      background-color: #f8f8f8; }
      .table__row:nth-child(odd):hover {
        background-color: #f4f4f4; }
    .table__row:nth-child(even) {
      background-color: #fff; }
      .table__row:nth-child(even):hover {
        background-color: #f4f4f4; }
    .table__row__id {
      box-sizing: border-box;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      padding: 17px 8px 15px 8px;
      width: 48px;
      min-height: 58px; }
      .table_sorting_alphabet .table__row:not(.table__row_first) .table__row__id {
        opacity: 0; }
    .table__row__image {
      box-sizing: border-box;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 9px;
      flex-shrink: 0; }
      .table__row__image > * {
        display: block;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        background-size: cover;
        background-repeat: no-repeat; }
    .table__row__title {
      box-sizing: border-box;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-grow: 1;
      padding: 17px 8px 15px 8px;
      min-height: 58px; }
    .table__row__rating {
      box-sizing: border-box;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      padding: 17px 8px 15px 8px;
      width: 64px;
      min-height: 58px;
      border-left-width: 2px;
      border-left-style: solid; }
      .table__row:nth-child(odd) .table__row__rating {
        border-left-color: #efefef; }
      .table__row:nth-child(even) .table__row__rating {
        border-left-color: #f6f6f6; }
