/* Contents */
.chart {
  display: block;
  margin: 24px;
  width: 688px;
  border-radius: 4px;
  background-color: #fff;
  padding-bottom: 18px; }
  .chart__header {
    box-sizing: border-box;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-grow: 1;
    padding: 17px 24px 24px 24px;
    font-weight: bold; }
  .chart .recharts-surface {
    padding: 0 24px 0 24px; }
  .chart .recharts-text {
    font-family: "Roboto", Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: 0.24px;
    color: #313131; }
