/* Contents */
.form-conditions {
  display: block; }
  .form-conditions__title {
    display: block;
    margin: 4px auto;
    font-weight: bold; }
  .form-conditions__list {
    display: block;
    margin: 4px auto;
    padding: 0;
    list-style-type: disc; }
  .form-conditions__item {
    position: relative;
    display: block;
    margin: 0 auto 0 .7em;
    padding: 0; }
    .form-conditions__item:before {
      content: "";
      position: absolute;
      display: block;
      right: 100%;
      top: 0;
      margin-right: .25em;
      font-size: larger;
      line-height: 1.5em; }
    .form-conditions__item_valid {
      color: #21b15e; }
    .form-conditions__item_invalid {
      color: #c83f3f; }
