/* Sizing */
/* Typography */
.text p {
  font-size: 17px;
  line-height: 22px; }

.text h1 {
  font-size: 88px;
  line-height: 92px; }

.text h2 {
  font-size: 56px;
  line-height: 66px;
  font-weight: bold; }

.text h3 {
  font-size: 0;
  line-height: 0; }

.text h4 {
  font-size: 34px;
  line-height: 41px;
  font-weight: bold; }

.text h5 {
  font-size: 32px;
  line-height: 38px; }

.text h6 {
  font-size: 22px;
  line-height: 25px;
  font-weight: bold; }

/* Colors */
/* Misc */
/* Contents */
.text {
  display: block;
  flex-shrink: 0;
  margin: auto;
  max-width: 672px; }
  .text h1 {
    display: block; }
  .text h2 {
    display: block;
    margin: 48px auto;
    text-align: left; }
  .text h3 {
    display: block; }
  .text h4 {
    display: block; }
  .text h5 {
    display: block; }
  .text h6 {
    display: block;
    margin: 16px auto;
    text-align: left; }
  .text p {
    display: block;
    margin: 24px auto;
    text-align: left; }
  .text a {
    transition: color 0.3s ease-out 0s; }
    .text a:not(:hover):not(:active):not(:focus), .text a:link:not(:hover):not(:active):not(:focus), .text a:visited:not(:hover):not(:active):not(:focus) {
      color: #584ac6;
      text-decoration: none; }
    .text a:hover, .text a:active, .text a:focus {
      color: #5448ba;
      text-decoration: underline; }
